.hero-img{
    width: 100%;
    background-color: rgba(0,0, 0, 0.5);
    height: 60vh;
    position: relative;
}

.hero-img::before {
    content: "";
    background: url("https://media.istockphoto.com/id/1068734692/photo/developing-programmer-development-website-design-and-coding-technologies-working-in-software.jpg?s=612x612&w=0&k=20&c=LmpHEaIHKcGGcBVjwe_mjOZyD38poNf5Vw-7E1gBDbQ=");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero-img h1{
    font-size: 2.4rem;
}

hero-img p{
    font-size: 1.4rem;
    text-align: center;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:640px) {
    .hero-img h1{
        font-size: 2rem;
    }
}
