
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color:rgba(83, 82, 82, 0.85);
    transition: 0.5s;
}

.nav-menus{
    display: flex;
}

.nav-menus li{
    padding: 0 1rem;
}

.nav-menus li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.humburger{
    display: none;
}

@media screen and (max-width:1040px){
    .nav-menus{
       flex-direction: column;
       justify-content: center;
       align-items: center;
       width: 100%;
       height: 100vh;
       background: rgba(0,0,0,0.9);
       position:absolute;
       top: 0;
       left: -100%;
       transition: 0.3s;
       z-index: -3;
    }

    .nav-menus.active{
        left: 0;
    }

    .nav-menus li{
        padding: 1rem 0;
    }

    .nav-menus a{
        font-size: 2rem;
    }
    .humburger{
        display: initial;
    }
}
